.side{font-family:"Roboto","Noto Sans KR",Arial,Helvetica,sans-serif;padding:10px;font-weight:500;font-size:.9rem}.side .recommend{margin-bottom:10px;color:#868e96}.side .user{width:100%;display:flex;padding:5px;align-items:center;justify-content:space-between}.side .user .user_info{display:flex;align-items:center;gap:10px}.side .user .user_info .user_image{width:40px;height:40px;border-radius:50%;object-fit:cover}.side .user .user_info .user_name{color:#495057}.side .user .visit{color:#5c7cfa;font-weight:600}.side .footer{font-size:.7rem;margin-top:30px;color:#adb5bd;font-weight:500}.side .footer .copy{margin-bottom:10px}.side .footer .link{color:#adb5bd}.side .footer .link::after{content:"•";margin:0 5px}.side .footer .link:last-child::after{content:none}